import React from 'react'
import dataEnglish from '../dataEnglish.json'
import dataFrench from '../dataFrench.json'

function Porfolio(props) {
    const isEnglish = props.isEnglish
    return (
        <section id="portfolio">
            <div className="row">
                <div className="twelve columns collapsed">
                    <h1>
                        <span role="img" aria-label="emoji-work-on-computer">
                            üë®‚Äçüíª
                        </span>{' '}
                        {isEnglish
                            ? 'Some of my Projects'
                            : 'Quelques-uns de mes projets'}{' '}
                        <span role="img" aria-label="emoji-work-on-computer">
                            üë®‚Äçüíª
                        </span>
                    </h1>
                    <div id="portfolio-wrapper">
                        {(isEnglish
                            ? dataEnglish.portfolio
                            : dataFrench.portfolio) &&
                            (isEnglish
                                ? dataEnglish.portfolio
                                : dataFrench.portfolio
                            ).map((item) => {
                                if (item.demolink === '') {
                                    return (
                                        <div className="columns portfolio-item">
                                            <h3>{item.name}</h3>
                                            <div className="description">
                                                <p>
                                                    Date : {item.date}
                                                    <br />
                                                    {isEnglish
                                                        ? 'Language(s)'
                                                        : 'Langage(s)'}{' '}
                                                    : {item.languages}
                                                    <br />
                                                    {isEnglish
                                                        ? 'Group of '
                                                        : 'Groupe de '}{' '}
                                                    {item.number}{' '}
                                                    {isEnglish
                                                        ? 'people'
                                                        : 'personne(s)'}
                                                </p>
                                                <p>{item.description}</p>
                                                <p>
                                                    <a
                                                        href={item.githublink}
                                                        rel="noopener noreferrer"
                                                        target="_blank"
                                                    >
                                                        {isEnglish
                                                            ? 'Github Link'
                                                            : 'Lien Github'}
                                                    </a>
                                                </p>
                                            </div>
                                        </div>
                                    )
                                } else {
                                    return (
                                        <div className="columns portfolio-item">
                                            <h3>{item.name}</h3>
                                            <div className="description">
                                                <p>
                                                    Date : {item.date}
                                                    <br />
                                                    {isEnglish
                                                        ? 'Language(s)'
                                                        : 'Langage(s)'}{' '}
                                                    : {item.languages}
                                                    <br />
                                                    {isEnglish
                                                        ? 'Group of '
                                                        : 'Groupe de '}{' '}
                                                    {item.number}{' '}
                                                    {isEnglish
                                                        ? 'people'
                                                        : item.number === '1'
                                                        ? 'personne'
                                                        : 'personnes'}
                                                </p>
                                                <p>{item.description}</p>
                                                <p>
                                                    <a
                                                        href={item.githublink}
                                                        rel="noopener noreferrer"
                                                        target="_blank"
                                                    >
                                                        {isEnglish
                                                            ? 'Github Link'
                                                            : 'Lien Github'}
                                                    </a>
                                                </p>
                                                <iframe
                                                    className="videodemo"
                                                    width="100%"
                                                    height="550"
                                                    src={item.demolink}
                                                    title="YouTube video player"
                                                    frameborder="0"
                                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                                    allowfullscreen
                                                ></iframe>
                                            </div>
                                        </div>
                                    )
                                }
                            })}
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Porfolio
